<template>
  <section class="Hot">
    <div class="header">
      <div class="header-icon"></div>
    </div>
    <ul>
      <MusicListItem
        @change-current-play-song="$emit('change-current-play-song', $event)"
        v-for="(item, index) in hotSongs"
        :key="item.id"
        :item="item"
        :index="index"
      />
    </ul>
  </section>
</template>

<script>
import MusicListItem from "../components/MusicListItem.vue";
export default {
  components: {
    MusicListItem,
  },
  data: function () {
    return {
      hotSongs: [],
    };
  },
  created() {
    this.axios.get("http://music.kele8.cn/top/list?idx=1").then((res) => {
      this.hotSongs = res.data.playlist.tracks;
    });
  },
};
</script>

}
